

button {
  color: var(--fg);
  font-size: .8rem;
  align-self: baseline;
}

.formulaWrapper {
  color: var(--fg);
  background-color: var(--bg);
  margin: 0;
  display: grid;
  padding: 0.5rem;
  grid-template-areas:
    "title title"
    "options options"
    "input input"
    "display display";
  grid-template-columns: auto 1fr;
  grid-template-rows: repeat(3, auto) 1fr;
  overflow-x: hidden;
  overflow-y: hidden;
}

.formulaWrapperRight {
  color: var(--fg);
  background-color: var(--bg);
  margin: 0;
  display: grid;
  padding: 0.5rem;
  grid-template-areas:
    "title title"
    "options options"
    "input display"
    "nothing display";
  grid-template-columns: auto 1fr;
  grid-template-rows: repeat(2, auto) 2fr 1fr;
  overflow-x: hidden;
  overflow-y: hidden;
}

.title {
  grid-area: title;
  font-size: 1.5rem;
  font-weight: bold;
}

.formulaOptions {
  grid-area: options;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  flex: 0 1 auto;
  margin: 0.2rem 0;
}

.formulaOptions > * {
  margin-right: 1rem;
  margin-bottom: 0.3rem;
  flex: 0 1 auto;
}

.formulaOptions > * > * {
  flex: 0 1 auto;
}

.formulaInput {
  grid-area: input;
}

.formulaInput > textarea {
  font-size: 1.2rem;
  width: 100%;
  border: 2px solid var(--fg);
  color: var(--fg);
  background-color:  var(--input-bg);
  outline: none;
}

.formulaInput > textarea:focus {
  font-weight: bold;
  color: var(--input-fg);
}

.formulaDisplayWrapper {
  grid-area: display;
  overflow: scroll;
  background-color: var(--display-bg);
  margin-top: 0.4rem;
}

.formulaDisplay {
  display: block;
  width: fit-content;
  height: fit-content;
  padding: 0.3rem;
  color: var(--display-fg);
  background-color: none;
}

.formulaDisplayError {
  padding: .2rem .4rem;
  grid-area: display;
  font-family: monospace;
  color: var(--error-fg);
}

@media only screen and (max-width: 720px) {
  :root {
    font-size: 0.9rem;
  }

  .title {
    font-size: 1rem;
  }

  .formulaInput > textarea {
    height: 5rem;
    font-size: 16px;
  }
}