html {
  height: 100%;
  margin: 0;
  overflow-x: hidden;
  overflow-y: scroll;
}

body {
  height: 100%;
  margin: 0;
  overflow: hidden;
  font-family: "open sans", -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

#root {
  height: 100%;
  margin: 0;
}

div {
  display: flex;
  flex: 1 1 100%;
  align-items: stretch;
}

code {
  font-family: Inconsolata, Menlo, Monaco, Consolas, "Courier New", monospace;
}

textarea {
  font-family: Inconsolata, Menlo, Monaco, Consolas, "Courier New", monospace;
}

:root {
  --fg: black;
  --bg: white;
  --input-fg: rgba(0,20,150,1);
  --input-bg: rgba(100,100,100,0.05);
  --display-fg: black;
  --display-bg: white;
  --error-fg: #777;
}


button {
  color: var(--fg);
  font-size: .8rem;
  align-self: baseline;
}

.FormulaEditor_formulaWrapper__2f4By {
  color: var(--fg);
  background-color: var(--bg);
  margin: 0;
  display: grid;
  padding: 0.5rem;
  grid-template-areas:
    "title title"
    "options options"
    "input input"
    "display display";
  grid-template-columns: auto 1fr;
  grid-template-rows: repeat(3, auto) 1fr;
  overflow-x: hidden;
  overflow-y: hidden;
}

.FormulaEditor_formulaWrapperRight__1w5jJ {
  color: var(--fg);
  background-color: var(--bg);
  margin: 0;
  display: grid;
  padding: 0.5rem;
  grid-template-areas:
    "title title"
    "options options"
    "input display"
    "nothing display";
  grid-template-columns: auto 1fr;
  grid-template-rows: repeat(2, auto) 2fr 1fr;
  overflow-x: hidden;
  overflow-y: hidden;
}

.FormulaEditor_title__2MKlg {
  grid-area: title;
  font-size: 1.5rem;
  font-weight: bold;
}

.FormulaEditor_formulaOptions__26V8n {
  grid-area: options;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  flex: 0 1 auto;
  margin: 0.2rem 0;
}

.FormulaEditor_formulaOptions__26V8n > * {
  margin-right: 1rem;
  margin-bottom: 0.3rem;
  flex: 0 1 auto;
}

.FormulaEditor_formulaOptions__26V8n > * > * {
  flex: 0 1 auto;
}

.FormulaEditor_formulaInput__3KtUb {
  grid-area: input;
}

.FormulaEditor_formulaInput__3KtUb > textarea {
  font-size: 1.2rem;
  width: 100%;
  border: 2px solid var(--fg);
  color: var(--fg);
  background-color:  var(--input-bg);
  outline: none;
}

.FormulaEditor_formulaInput__3KtUb > textarea:focus {
  font-weight: bold;
  color: var(--input-fg);
}

.FormulaEditor_formulaDisplayWrapper__2PtTs {
  grid-area: display;
  overflow: scroll;
  background-color: var(--display-bg);
  margin-top: 0.4rem;
}

.FormulaEditor_formulaDisplay__2XK4C {
  display: block;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  padding: 0.3rem;
  color: var(--display-fg);
  background-color: none;
}

.FormulaEditor_formulaDisplayError__3v1E4 {
  padding: .2rem .4rem;
  grid-area: display;
  font-family: monospace;
  color: var(--error-fg);
}

@media only screen and (max-width: 720px) {
  :root {
    font-size: 0.9rem;
  }

  .FormulaEditor_title__2MKlg {
    font-size: 1rem;
  }

  .FormulaEditor_formulaInput__3KtUb > textarea {
    height: 5rem;
    font-size: 16px;
  }
}

